:root {
    --surface-color: #FFf;
    --curve: 10;
    --col: #FE5F1E;
    --col2: rgb(87, 85, 85);
  }
  html {
    scroll-behavior: smooth;
  }
  * {
    box-sizing: border-box;
  }
  
  body {
    /* font-family: 'Lato', sans-serif; */
      
  }
  .font{
    font-family: "MockFlowFont"; 
  }
  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    margin: 4rem 5vw;
    padding: 0;
    list-style-type: none;
  }
  
  .card {
    position: relative;
    display: block;
    height: 100%;  
    border-radius: calc(var(--curve) * 1px);
    overflow: hidden;
    text-decoration: none;
  }
  
  .card__image {      
    width: 100%;
    height: auto;
  }
  
  .card__overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;      
    border-radius: calc(var(--curve) * 1px);    
    background-color: var(--surface-color);      
    transform: translateY(100%);
    transition: .2s ease-in-out;
  }
  
  .card:hover .card__overlay {
    transform: translateY(0);
  }
  
  .card__header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 2em;
    padding: 2em;
    border-radius: calc(var(--curve) * 1px) 0 0 0;    
    background-color: var(--surface-color);
    transform: translateY(-100%);
    transition: .2s ease-in-out;
  }
  
  .card__arc {
    width: 80px;
    height: 80px;
    position: absolute;
    bottom: 100%;
    right: 0;      
    z-index: 1;
  }
  
  .card__arc path {
    fill: var(--surface-color);
    d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
  }       
  
  .card:hover .card__header {
    transform: translateY(0);
  }
  
  .card__thumb {
    flex-shrink: 0;
    width: 50px;
    height: 50px;      
    border-radius: 50%;      
  }
  
  .card__title {
    font-size: 1em;
    margin: 0 0 .3em;
    color: #6A515E;
  }
  
  .card__tagline {
    display: block;
    margin: 1em 0;
    font-family: "MockFlowFont";  
    font-size: .8em; 
    color: #D7BDCA;  
  }
  
  .card__status {
    font-size: .8em;
    color: #D7BDCA;
  }
  
  .card__description {
    padding: 0 2em 2em;
    margin: 0;
    color: #D7BDCA;
    font-family: "MockFlowFont";   
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }    
  .main-text{
    color:Grey;
    font-size:30px;
  }
  .deco {
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 5px 5px 0 rgba(31, 38, 135, 0.37);
    border-radius: 20px;
    border: 0.5px solid rgba(255, 255, 255, 0.18);
  }
  
  .nu {
    font-size: 11px;
    color: #757575;
  }
  
  .bd {
    text-align: center;
  }
  
  .hh {
    font-size: 14px;
    color: #757575;
    font-weight: bolder;
  }
  
  p {
    font-size: 11px;
    color: #878787;
    line-height: 22px;
    text-align: center;
  }
  
  a {
    text-decoration: none;
  }
  
  h3 {
    font-size: 4em;
    margin-top: -10px;
    letter-spacing: 2px;
    color: rgb(87, 85, 85);
    /* text-align: center; */
    font-weight: bolder;
  }

  
  .d-flex {
    display: flex;
  }
  
  .align-center {
    align-items: center;
  }
  
  .flex-centerY-centerX {
    justify-content: center;
    justify-content: center;
    align-items: center;
  }
  
  .page-wrapper {
    height: 100%;
  }
  
  .el-wrapper {
    width: 257px;
    padding: 15px;
    margin: auto 5px;
    margin-bottom: 7px;
    background-color: #fff;
  }
  
  @media (max-width: 991px) {
    .el-wrapper {
      width: 257px;
    }
  }
  @media (max-width: 767px) {
    .el-wrapper {
      width: 257px;
    }
  }
  .el-wrapper:hover .h-bg {
    left: 0px;
  }
  
  .el-wrapper:hover .price {
    left: 20px;
    transform: translateY(-50%);
    color: #818181;
  }
  
  .el-wrapper:hover .add-to-cart {
    left: 50%;
  }
  
  .el-wrapper:hover .img {
    webkit-filter: blur(7px);
    -o-filter: blur(7px);
    -ms-filter: blur(7px);
    filter: blur(7px);
    filter: progid:DXImageTransform.Microsoft.Blur(pixelradius="7", shadowopacity="0.0");
    opacity: 0.4;
  }
  
  .el-wrapper:hover .info-inner {
    bottom: 155px;
  }
  
  .el-wrapper:hover .a-size {
    transition-delay: 300ms;
    bottom: 50px;
    opacity: 1;
  }
  
  .el-wrapper .box-down {
    width: 100%;
    height: 60px;
    position: relative;
    overflow: hidden;
  }
  
  .el-wrapper .box-up {
    width: 100%;
    height: 260px;
    position: relative;
    overflow: hidden;
    text-align: center;
  }
  
  .el-wrapper .img {
    padding: 7px 29px;
    transition: all 800ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    width:100%;
    height:auto;
  }
  
  
  .h-bg {
    transition: all 800ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    width: 660px;
    height: 100%;
    background-color: #FE5F1E;
    position: absolute;
    left: -659px;
  }
  
  .h-bg .h-bg-inner {
    width: 50%;
    height: 100%;
    background-color: #464646;
  }
  
  .info-inner {
    transition: all 400ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    position: absolute;
    width: 100%;
    bottom: 25px;
  }
  
  .info-inner .p-name,
  .info-inner .p-company {
    display: block;
  }
  
  .info-inner .p-name {
    font-family: "PT Sans", sans-serif;
    font-size: 18px;
    color: #252525;
  }
  
  .info-inner .p-company {
    font-family: "Lato", sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    color: #8c8c8c;
  }
  
  .a-size {
    transition: all 300ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    position: absolute;
    width: 100%;
    bottom: -20px;
    font-family: "PT Sans", sans-serif;
    color: #828282;
    opacity: 0;
  }
  
  .a-size .size {
    color: #252525;
  }
  .cart {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    font-family: "Lato", sans-serif;
    font-weight: 700;
  }
  
  .cart .price {
    transition: all 600ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-delay: 100ms;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
    color: #252525;
  }
  
  .cart .add-to-cart {
    transition: all 600ms cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-timing-function: cubic-bezier(0, 0, 0.18, 1);
    /* ease-out */
    transition-delay: 100ms;
    display: block;
    position: absolute;
    top: 50%;
    left: 110%;
    transform: translate(-50%, -50%);
  }
  
  .cart .add-to-cart .txt {
    font-size: 12px;
    color: #fff;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    white-space: nowrap;
  }

  .btn {
    background: #FE5F1E;
  }
  .btn:hover {
    transition: all 0.3s ease-in-out;
    background-color: #b23301;
  }
  .btn-nav{
    color: rgb(87, 85, 85);
    text-decoration:none;
  }
  .banner{
    max-width:100%;
    max-height:290px;
    text-align: center;
  }
  .ban-center{
    text-align: center;
    margin: auto;
  }